<script>
   /*************************************************************
   * AppPlots component for test set                            *
   * -------------------------------                            *
   * show selection of plot for testing of SIMCA model          *
   **************************************************************/

   // import plot and plot controls components
   import AppPlots  from '../shared/AppPlots.svelte';

   import { SensitivityPlot, SensitivityPlotControls } from '../plots';
   import { ExtremePlot, ExtremePlotControls } from '../plots';
   import { AcceptancePlot, AcceptancePlotControls } from '../plots';
   import { DistancePlot, DistancePlotControls } from '../plots';


   // Input parameters
   export let simcares;
   export let simca;


   // array with list of plots and their components
   let options = [
      {label: 'Acceptance', plotComponent: AcceptancePlot, controlsComponent: AcceptancePlotControls},
      {label: 'Distance', plotComponent: DistancePlot, controlsComponent: DistancePlotControls},
      {label: 'Sensitivity', plotComponent: SensitivityPlot, controlsComponent: SensitivityPlotControls},
      {label: 'Extremes', plotComponent: ExtremePlot, controlsComponent: ExtremePlotControls},
   ];

</script>

{#if options}
<AppPlots on:setoutlier on:plotoutlier {simcares} {simca} {options}/>
{/if}