<script>
   /****************************************************************
   * SpecificityPlot                                               *
   * --------------------                                          *
   * shows plot with specificity values vs. number of PCs          *
   *****************************************************************/

   import { Vector } from 'mdatools/arrays';
   import { Axes, XAxis, YAxis, Box, Lines, Segments, Points } from 'svelte-plots-basic/2d';

   export let params;                     // JSON with main plot parameters
   export let simcares;                   // JSON with SIMCA results
   export let simca;                      // JSON with SIMCA parameters


   /*****************************************/
   /* Component code                        */
   /*****************************************/

   // constant plot parameters
   const margins = [0.85, 1.0, 0.25, 0.25];
   const showGrid = true;                  // logical, show grid or not
   const xLabel = 'Number of PCs';
   const yLabel = 'Specificity';
   const title = 'Specificity plot';


   // local copies of plot parameters
   let resID = simcares ? Object.keys(simcares)[0] : '';
   $: if (params && params.resID !== resID) resID = params.resID;

   // select object with current results
   $: resobj = simcares && resID ? simcares[resID] : null;

   $: a = simca ? simca.ncomp : null;
   $: y = resobj ? resobj.v.map(v => v.stat.specificity) : null;
   $: x = y ? Vector.seq(1, y.length) : null;
   $: yMin = params && params.zoom === 'on' ? Math.min(...y) - 0.1 : -0.1;
   $: yMax = params && params.zoom === 'on' ? 1.05 : 1.1;
</script>

{#if params, x, y }
<Axes {margins} {title} {xLabel} {yLabel}
   limX={[0, x.length + 1]}
   limY={[yMin, yMax]}>

   <Segments xStart={[a]} xEnd={[a]} yStart={[-1]} yEnd={[1.1]} lineColor='#00000040' />

   <Lines xValues={x} yValues={y} lineWidth={1.5} lineColor='#2679B2' />
   <Points xValues={x} yValues={y} borderWidth={1.5} faceColor='#fff' borderColor='#2679B2'/>
   <Points xValues={[x.v[a - 1]]} yValues={[y[a - 1]]} borderWidth={4} faceColor='#2679B2' borderColor='#2679B2'/>


   <XAxis {showGrid} slot="xaxis" />
   <YAxis {showGrid} slot="yaxis" />
   <Box slot="box" />
</Axes>
{/if}