<script>
   /*************************************************************
   * AcceptancePlot for test set                                *
   * ---------------------------                                *
   * shows plot with score and orthogonal distances             *
   * and acceptance areas                                       *
   **************************************************************/

   import { max } from 'mdatools/stat';
   import { Axes, XAxis, YAxis, Box } from 'svelte-plots-basic/2d';

   import AcceptancePlotSeries from './AcceptancePlotSeries.svelte';
   import AcceptancePlotLegend from './AcceptancePlotLegend.svelte';
   import AcceptancePlotLimits from './AcceptancePlotLimits.svelte';

   export let params;                     // JSON with main plot parameters
   export let simcares;                   // JSON with SIMCA results
   export let simca;                      // JSON with SIMCA parameters


   /*****************************************/
   /* Component code                        */
   /*****************************************/

   // constant plot parameters
   const margins = [0.85, 1.0, 0.25, 0.25];
   const showGrid = true;

   // colors for series
   const SERIES_COLORS = ['#2679B2', '#D22C2F']; // in / out

   // local copies of plot parameters
   let distType = 'h/q';
   let transType = 'no';
   let showLabels = 'no';

   // local copies of SIMCA parameters
   let ncomp = 1;

   // max values for distance points coordinates
   let psXMax = 1;
   let psYMax = 1;

   // max values for limit lines coordinates
   let lmXMax = 1;
   let lmYMax = 1;

   // function for creating axis titles
   function get_axis_label(id, transType) {
      const distNames = {'q': 'Orthogonal distance', 'h': 'Score distance', 'f': 'Full distance'};
      return distNames[id] + ', ' + (transType === 'log' ? 'log(1 + ' + id + '/' + id + '0)' : id + '/' + id + '0');
   };

   // reactive expressions to monitor changes in plot and model parameters
   $: if (params && params.distType !== distType) distType = params.distType;
   $: if (params && params.transType !== transType) transType = params.transType;
   $: if (params && params.showLabels !== showLabels) showLabels = params.showLabels;
   $: if (simca && simca.ncomp !== ncomp) ncomp = simca.ncomp;

   // select object with current results
   $: resobj = simcares  ? simcares[Object.keys(simcares)[0]] : null;

   // reactive calculations for axis labels and title
   $: title = 'Acceptance plot (A = ' + ncomp + ')';
   $: xLabel = distType === 'f' ? 'Objects' : get_axis_label('h', transType);
   $: yLabel = distType === 'h/q' ? get_axis_label('q', transType) : get_axis_label('f', transType);

   // reactive calculations for axis limits
   $: xMax = max([psXMax, lmXMax]);
   $: yMax = max([psYMax, lmYMax]);
   $: limX = [-0.02 * xMax, 1.05 * xMax];
   $: limY = [-0.02 * yMax, 1.20 * yMax];
</script>

{#if resobj && params}
<Axes {margins} {xLabel} {yLabel} {limX} {limY} {title} >

   <AcceptancePlotSeries
      bind:xMax={psXMax}
      bind:yMax={psYMax}
      colors={SERIES_COLORS}
      res={resobj.v[ncomp - 1]}
      labels={resobj.labels}
      classes={resobj.classes}
      targetClass={simca.className}
      {distType}
      {transType}
      {showLabels}
   />

   <AcceptancePlotLimits {simca} {transType} {distType} bind:xMax={lmXMax} bind:yMax={lmYMax} />
   <AcceptancePlotLegend categories={resobj.v[ncomp - 1].anum} colmap={SERIES_COLORS} />

   <XAxis {showGrid} slot="xaxis" />
   <YAxis {showGrid} slot="yaxis" />
   <Box slot="box" />
</Axes>
{/if}

