<script>
   /****************************************************************
   * ExtremePlot                                                   *
   * --------------------                                          *
   * shows plot with number of extreme samples for different alpha *
   *****************************************************************/

   import { pchisq } from 'mdatools/distributions';
   import { Vector } from 'mdatools/arrays';
   import { Axes, XAxis, YAxis, Box, Lines, Segments, Legend, Points } from 'svelte-plots-basic/2d';

   export let params;                                    // JSON with main plot parameters
   export let simcares;                                  // JSON with SIMCA results
   export let simca;                                     // JSON with SIMCA parameters
   export let selected;
   export let showGrid = true;                           // logical, show grid or not
   export let xLabel = 'Number of extremes (expected)';  // label for x axis
   export let yLabel = 'Number of extremes (observed)';  // label for y axis


   /*****************************************/
   /* Component code                        */
   /*****************************************/


   // constant parameters
   const margins = [0.85, 1.0, 0.25, 0.25];
   const LINE_COLORS = {'cal': '#2679B2', 'pv': '#D22C2F', 'target': '#2679B2', 'alt': '#2679B2'};
   const FACE_COLORS = {'cal': '#f0f8ff', 'pv': '#fff8f0', 'target': '#f0f8ff', 'alt': '#f0f8ff'};

   // local copies of Plot parameters
   let resID = simcares ? Object.keys(simcares)[0] : '';

   // function to compute observed extremes
   function getObserved(res, id) {
      const v = res.v[res.ncomp - 1];
      const n = v.f.length;
      let observed = Vector.zeros(n);
      for (let i = 0; i < n; i++) {
         const pi = pchisq(v.f[i], v.Nf);
         observed = observed.add((alpha.apply(v => 1 - pi < v)));
      }

      const legendItem = {label: id, marker: 1, borderColor: LINE_COLORS[id], faceColor: FACE_COLORS[id]};
      const lineColor = LINE_COLORS[id];
      const faceColor = FACE_COLORS[id];

      return {
         values: observed,
         legendItem,
         lineColor,
         faceColor
      };
   }

   $: resIDs = simcares ? Object.keys(simcares) : null;
   $: a = simca ? simca.ncomp : null;
   $: title = a ? 'Extremes plot (A = ' + a + ')' : '';
   $: nObj = resIDs ? simcares[resIDs[0]].v[a - 1].f.length : null;


   let observed, legendItems;
   $: if (resIDs && params && params.resID) {
      observed = params.resID === 'both' ?
         resIDs.map(id => getObserved(simcares[id], id)) :
         [getObserved(simcares[params.resID], params.resID)];
      legendItems = observed.map(o => o.legendItem);
   } else {
      observed = null;
      legendItems = null;
   }


   // compute expected values values
   let expected, Nm, Np, alpha;
   $: if (nObj) {
      alpha = Vector.zeros(nObj);
      expected = Vector.zeros(nObj);
      Nm = Vector.zeros(nObj);
      Np = Vector.zeros(nObj);

      for (let i = 1; i <= nObj; i++) {
         const a = i / nObj;
         const D = 2 * Math.sqrt(i * (1 - a));
         Nm.v[i - 1] = i - D;
         Np.v[i - 1] = i + D;
         alpha.v[i - 1] = a;
         expected.v[i - 1] = i;
      }

   } else {
      expected = null;
      Nm = null;
      Np = null;
      alpha = null;
   }
</script>

{#if expected && observed }
<Axes {margins} {title} {xLabel} {yLabel}
   limX={[-1, nObj + 2]}
   limY={[-1, nObj + 2]}>

   <Lines xValues={expected} yValues={expected} lineColor="#e0e0e0" />
   <Lines xValues={expected} yValues={Nm} lineColor="#e0e0e0" />
   <Lines xValues={expected} yValues={Np} lineColor="#e0e0e0" />
   <Segments xStart={expected} xEnd={expected} yStart={Nm} yEnd={Np}  lineColor="#e0e0e0"/>

   {#each observed as o}
   <Points xValues={expected} yValues={o.values} borderWidth={1.5} borderColor={o.lineColor} />
   {/each}

   {#if legendItems && legendItems.length > 0}
   <Legend items={legendItems} position='bottomright'/>
   {/if}

   <XAxis {showGrid} slot="xaxis" />
   <YAxis {showGrid} slot="yaxis" />
   <Box slot="box" />
</Axes>
{/if}