<script>
   /*************************************************************
   * DistancePlot                                               *
   * --------------------                                       *
   * shows plot with score, orthogonal or full distances        *
   **************************************************************/

   import { Vector } from 'mdatools/arrays';
   import { max } from 'mdatools/stat';
   import { qchisq } from 'mdatools/distributions';
   import { Axes, XAxis, YAxis, Box, Legend, Points, Lines, TextLabels, Bars, Rectangles } from 'svelte-plots-basic/2d';
   import { LINE_COLORS, FACE_COLORS, LABELS_COLOR, LABELS_COLOR_SELECTED } from '../utils';

   export let params;                     // JSON with main plot parameters
   export let simcares;                   // JSON with SIMCA results
   export let simca;                      // JSON with SIMCA parameters
   export let selected;                   // selected sample

   /*****************************************/
   /* Component code                        */
   /*****************************************/

   // constant plot parameters
   const DIFF_COLOR = '#b0b0b0';
   const margins = [0.85, 1.0, 0.25, 0.25];
   const showGrid = true;
   const LINE_COLOR = '#909090';    // color of the lines

   // local copies of plot parameters
   let distType = 'h';
   let showLabels = 'no';
   let ncomp = 1;
   let resID = 'both';
   let yMax = 0;


   /**
    * Get limits for the selected distance type
    * @param res - SIMCA result object
    * @param distType - distance type
    */
   function getLimits(res, distType) {
      if (!res) return null;
      const a = res.ncomp - 1;
      const Nu = res.v[a]['N' + distType];
      const alpha = res.alpha;
      const lim = qchisq(1 - alpha/100, Nu) / Nu;

      if (yMax < lim) yMax = lim;
      return lim
   }


   /**
    * Generate plot series for given result object
    * @param res - SIMCA result object
    * @param id - result ID
    */
   function getSeries(res, id) {
      if (!res || !id) return null;

      // get result values for selected number of components
      const r = res.v[res.ncomp - 1];

      // generate x vector
      const n = r.h.length;
      const x = Vector.seq(1, n);

      // get a copy of the array with distance values and its scalar u0
      const u = r[distType].slice();
      const u0 = distType === 'f' ? r['N' + distType] : r[distType + '0'];

      // normalize the distance values and find the maximum
      let maxu = 0;
      for (let i = 0; i < n; i++) {
         u[i] = u[i]/u0;
         if (maxu < u[i]) maxu = u[i];
      }

      // update the global maximum for y-axis
      yMax = maxu;

      // create legend item
      const legendItem = {label: id, marker: 1, borderColor: LINE_COLORS[id], faceColor: FACE_COLORS[id]};

      // get proper colors
      const lineColor = LINE_COLORS[id];
      const faceColor = FACE_COLORS[id];

      // return series object
      return {id, x, y: new Vector(u), labels: res.labels, legendItem, lineColor, faceColor};
   }


   /**
    * Get axis label for given distance type
    * @param id - distance type
    */
   function get_axis_label(id) {
      const distNames = {'q': 'Orthogonal distance', 'h': 'Score distance', 'f': 'Full distance'};
      return distNames[id] + ', ' +  id + '/' + id + '0';
   };


   /**
    * Function to handle selection of a point
    * @param e - event object
    */
   function selectObject(e) {
      const id = parseInt(e.detail.elementID)
      const title = e.detail.seriesTitle
      if (id >= 0 && (title === 'diff' || title === 'pv' || title === 'cal' || title === 'target')) {
         selected = id;
      }
   }


   /**
    * Function to reset selection
    * @param e - event object
    */
   function resetSelection(e) {
      selected = -1;
   }


   // reactive expressions to monitor changes in plot and model parameters
   $: if (params && params.resID !== resID) resID = params.resID;
   $: if (params && params.distType !== distType) distType = params.distType;
   $: if (params && params.showLabels !== showLabels) showLabels = params.showLabels;
   $: if (simca && simca.ncomp !== ncomp) ncomp = simca.ncomp;

   // get IDs of all provided result objects and number of observations in the first object
   $: resIDs = simcares ? Object.keys(simcares) : null;
   $: nObj = simcares ? simcares[resIDs[0]].nrows : 0;

   // create series for the selected result objects
   let series, legendItems;
   $: if (resIDs && resID && distType) {
      series = resID === 'both' || resID === 'diff' ?
         resIDs.map(id => getSeries(simcares[id], id)) :
         [getSeries(simcares[resID], resID)];
      legendItems = series.map(s => s.legendItem);
   } else {
      series = null;
      legendItems = null;
   }

   // if resID is 'diff' compute absolute difference between the first two series
   let diff;
   $: if (series && resID === 'diff' && series.length === 2) {
         const d = Vector.zeros(series[0].y.length);
         for (let i = 0; i < d.length; i++) {
            d.v[i] = Math.abs(series[0].y.v[i] - series[1].y.v[i]);
         }
         diff = d;
         yMax = max(diff);
         legendItems = [{label: 'abs(cal - pv)', marker: 1, borderColor: 'transparent', faceColor: DIFF_COLOR}];
      };

   // get limits for the selected distance type
   $: lim = simcares && resID && resID !== 'diff' && distType ? getLimits(simcares[resIDs[0]], distType) : null;

   // reactive calculations for axis labels and title
   $: title = 'Distance plot (A = ' + ncomp + ')';
   $: xLabel = 'Objects';
   $: yLabel = distType ? get_axis_label(distType) : '';

   // reactive calculations for axis limits
   $: limX = [0 - nObj * 0.05, nObj * 1.05];
   $: limY = [0 - yMax * 0.05, 1.20 * yMax];
</script>

{#if simcares && params}
<div class="plot-container">
<Axes {margins} {xLabel} {yLabel} {limX} {limY} {title}
   on:markerclick={selectObject}
   on:barclick={selectObject}
   on:axesclick={resetSelection}
>
   {#if lim}
   <Lines xValues={limX} yValues={[lim, lim]} lineColor={LINE_COLOR} lineType={2} />
   {/if}
   {#if resID !== 'diff'}

      <!-- distance points -->
      {#each series as s}
      <Points title={s.id} xValues={s.x} yValues={s.y} borderWidth={1.5} faceColor={s.faceColor} borderColor={s.lineColor}/>
      {/each}

      <!-- labels on top of each point -->
      {#if showLabels === 'show'}
         {#each series as s}
         <TextLabels xValues={s.x} yValues={s.y} labels={s.labels} pos={3} faceColor={LABELS_COLOR} />
         {/each}
      {/if}

      <!-- for selection highlight selected point and dim the others -->
      {#if selected >= 0}
         <Rectangles left={[limX[0]]} top={[limY[1]]} width={[limX[1] - limX[0]]} height={[limY[1] - limY[0]]}
            borderColor='transparent' faceColor='#ffffffa0' />
         {#each series as s}
            <Points title={s.id}
               markerSize={1.25}
               xValues={[s.x.v[selected]]}
               yValues={[s.y.v[selected]]}
               borderWidth={3.0} faceColor={s.faceColor} borderColor={s.lineColor}
            />
         {/each}

         {#if showLabels === 'show'}
            {#each series as s}
               <TextLabels
                  xValues={[s.x.v[selected]]}
                  yValues={[s.y.v[selected]]}
                  labels={[s.labels[selected]]}
                  pos={3} faceColor={LABELS_COLOR_SELECTED}
               />
            {/each}
         {/if}

      {/if}

   {:else}

      <!-- bars with difference values -->
      <Bars title='diff' xValues={series[0].x} yValues={diff} faceColor={DIFF_COLOR} borderColor='transparent'/>

      <!-- labels on top of each bar -->
      {#if showLabels === 'show'}
         <TextLabels xValues={series[0].x} yValues={diff} labels={series[0].labels} pos={3} faceColor={LABELS_COLOR} />
      {/if}

      <!-- for selection highlight selected bar and dim the others -->
      {#if selected >= 0}

         <Rectangles left={[limX[0]]} top={[limY[1]]} width={[limX[1] - limX[0]]} height={[limY[1] - limY[0]]}
            borderColor='transparent' faceColor='#ffffffa0' />

         <Rectangles title='diff'
            left={[series[0].x.v[selected] - 0.5]}
            top={[diff.v[selected]]}
            width={1}
            height={[diff.v[selected]]}
            faceColor='#ff0000'
            borderColor='transparent'
         />

         {#if showLabels === 'show'}
            <TextLabels
               xValues={[series[0].x.v[selected]]}
               yValues={[diff.v[selected]]}
               labels={[series[0].labels[selected]]}
               pos={3}
               faceColor={LABELS_COLOR_SELECTED}
            />
         {/if}

      {/if}

   {/if}


   {#if legendItems && legendItems.length > 0}
   <Legend items={legendItems} position='topright'/>
   {/if}

   <XAxis {showGrid} slot="xaxis" />
   <YAxis {showGrid} slot="yaxis" />
   <Box slot="box" />
</Axes>
</div>
{#if selected < 0}
<div class="msg-select">Click on a {resID === 'diff' ? 'bar' : 'point' } to select...</div>
{:else}
<div class="msg-select">Click anywhere to remove selection...</div>
{/if}

{/if}


<style>
   .plot-container {
      width: 100%;
      height: calc(100% - 15px);
   }
   .msg-select {
      height: 15px;
      font-size: 12px;
      color: #aaa;
      text-align: center;
   }
</style>

