<script>
   /*************************************************************
   * AcceptancePlotLimits                                       *
   * --------------------                                       *
   * shows plot with acceptance limits for extreme and outliers *
   **************************************************************/

   import { Lines } from 'svelte-plots-basic/2d';
   import { Vector, vector } from 'mdatools/arrays';

   export let simca;                     // JSON with SIMCA parameters
   export let transType = 'no';          // transformation type ('log' or 'none')
   export let distType = 'h/q';          // distance type ('h/q' or 'f')
   export let xMax = 0;                  // max value for x-coordinates of limits
   export let yMax = 0;                  // max value for y-coordinates of limits


   /*****************************************/
   /* Component code                        */
   /*****************************************/

   const NPOINTS = 100;             // number of points in limit lines
   const LINE_COLOR = '#909090';    // color of the lines


   // function computes x and y coordinates for given critical value
   function get_limit_coordinates(crit, Nh, Nq, transType, distType) {

      if (distType === 'f') {
         // show limit for full distance
         return {
            x: vector([0, 1000]),
            y: transType === 'log' ? vector([Math.log(1 + crit), Math.log(1 + crit)]) : vector([crit, crit])
         }
      }

      // show limit for orthogonal and score distance
      const b0 = crit/Nq;
      const b1 = -Nh / Nq;
      const xMax = -b0 / b1;
      const x = Vector.seq(0, xMax * 1, xMax  / NPOINTS);
      const y = x.apply(v => v * b1 + b0);
      return {
         x:  transType === 'log' ? x.apply(v => Math.log(1 + v)) : x,
         y:  transType === 'log' ? y.apply(v => Math.log(1 + v)) : y
      }
   }

   let els = null, ols = null;
   $: if (simca) {
      const a = simca.ncomp - 1;
      els = get_limit_coordinates(simca.eCrit[a], simca.Nh[a], simca.Nq[a], transType, distType);
      ols = get_limit_coordinates(simca.oCrit[a], simca.Nh[a], simca.Nq[a], transType, distType);
      xMax = distType === 'h/q' ? ols.x.v[NPOINTS - 1] : 1;
      yMax = ols.y.v[0];
   } else {
      els = null;
      ols = null;
      xMax = 1;
      yMax = 1;
   }
</script>

{#if els }
<Lines xValues={els.x} yValues={els.y} lineColor={LINE_COLOR} lineType={2} />
{/if}

{#if ols }
<Lines xValues={ols.x} yValues={ols.y} lineColor={LINE_COLOR} lineType={3} />
{/if}
