<script>
   /*************************************************************
   * SIMCASettings                                              *
   * -------------------------------------                      *
   * shows panel with SIMCA model settings.                     *
   **************************************************************/

   import { Select, Container, Range } from '../controls';
   import { qchisq } from 'mdatools/distributions';


   export let model;
   export let simca;

   /*****************************************/
   /* Component code                        */
   /*****************************************/


   // parameters to change
   let limType = simca ? simca.limType : 'classic';
   let ncomp = simca ? simca.ncomp : 1;
   let alpha = simca ? simca.alpha : 5;
   let gamma = simca ? simca.gamma : 1;


   // possible choices for alpha and gamma
   const alphaOptions = [10, 5, 1, 0.1];
   const gammaOptions = [5, 1, 0.1, 0.01];


   /*****************************************/
   /* functions for changing model settings */
   /*****************************************/

   function setSimcaNcomp(ncomp) {
      if (simca.ncomp === ncomp) return;
      simca.ncomp = ncomp;
   }

   function setSimcaAlpha(alpha) {
      if (alpha === simca.alpha) return;
      simca.alpha =  alpha;
      simca.eCrit = simca.Nf.map(v => qchisq(1 - alpha / 100, v));
   }

   function setSimcaGamma(gamma) {
      if (gamma === simca.gamma) return;
      simca.gamma = gamma;
      simca.oCrit = simca.Nf.map(v => qchisq(Math.pow(1 - gamma / 100, 1 / model.nrows), v));
   }

   function setSimcaLimType(limType) {
      if (!simca || !model || limType === simca.limType) return;
      simca.limType = limType;
      simca.Nh = model.lim[limType].Nh;
      simca.h0 = model.lim[limType].h0;
      simca.Nq = model.lim[limType].Nq;
      simca.q0 = model.lim[limType].q0;
      simca.Nf = simca.Nh.map((v, i) => v + simca.Nq[i]);
      simca.eCrit = simca.Nf.map(v => qchisq(1 - simca.alpha / 100, v));
      simca.oCrit = simca.Nf.map(v => qchisq(Math.pow(1 - simca.gamma / 100, 1 / model.nrows), v));
   }

   /*****************************************/
   /* functions for changing local settings */
   /*****************************************/

   function setNComp(simcaNcomp) {
      if (ncomp === simcaNcomp) return;
      ncomp = simcaNcomp;
   }

   function setAlpha(simcaAlpha) {
      if (alpha === simcaAlpha) return;
      alpha = simcaAlpha;
   }

   function setGamma(simcaGamma) {
      if (gamma === simcaGamma) return;
      gamma = simcaGamma;
   }

   function setLimType(simcaLimType) {
      if (limType === simcaLimType) return;
      limType = simcaLimType;
   }


   // reactive expressions to change model settings based on local values
   $: setSimcaNcomp(ncomp);
   $: setSimcaAlpha(alpha);
   $: setSimcaGamma(gamma);
   $: setSimcaLimType(limType);

   // reactive expressions to change local values based on model settings
   $: setNComp(simca.ncomp);
   $: setAlpha(simca.alpha);
   $: setGamma(simca.gamma);
   $: setLimType(simca.limType);
</script>

<Container label="">
   <p class="title">Target class: {simca.className}</p>
</Container>

<Container label="# of PCs">
   <Range id="ncomp" bind:value={ncomp} min={1} max={model.ncomp} step={1} decNum={0} />
</Container>

<Container label='Alpha, %:'>
   <Select id='alpha' bind:value={alpha} options={alphaOptions} />
</Container>

<Container label='Gamma, %:'>
   <Select id='gamma' bind:value={gamma} options={gammaOptions} />
</Container>

<Container label='Limits:'>
   <Select id='limType' bind:value={limType} options={['classic', 'robust']} />
</Container>

