<script>
   /*************************************************************
   * ModelLoader                                                *
   * -------------------------                                  *
   * loads SIMCA model from file                                *
   **************************************************************/

   import { FileSelector, Container, ButtonCancel, Button } from '../controls';

   // calibration set
   export let model;
   export let simca;


   // JSON file with model
   let modelFile = undefined;

   // app status for handling and showing errors
   let status = 'neutral';
   let errMsg = '';


   // gets model content as one large string, parse it into matrix
   function setModel(strData) {
      try{
         const m = JSON.parse(strData);
         m.pca.v = new Float64Array(m.pca.v);

         m.pca.lim.classic.Nh = new Float64Array(m.pca.lim.classic.Nh);
         m.pca.lim.classic.Nq = new Float64Array(m.pca.lim.classic.Nq);
         m.pca.lim.classic.h0 = new Float64Array(m.pca.lim.classic.h0);
         m.pca.lim.classic.q0 = new Float64Array(m.pca.lim.classic.q0);

         m.pca.lim.robust.Nh = new Float64Array(m.pca.lim.robust.Nh);
         m.pca.lim.robust.Nq = new Float64Array(m.pca.lim.robust.Nq);
         m.pca.lim.robust.h0 = new Float64Array(m.pca.lim.robust.h0);
         m.pca.lim.robust.q0 = new Float64Array(m.pca.lim.robust.q0);

         m.simca.h0 = new Float64Array(m.simca.h0);
         m.simca.Nh = new Float64Array(m.simca.Nh);
         m.simca.q0 = new Float64Array(m.simca.q0);
         m.simca.Nq = new Float64Array(m.simca.Nq);
         m.simca.Nf = new Float64Array(m.simca.Nf);
         m.simca.eCrit = new Float64Array(m.simca.eCrit);
         m.simca.oCrit = new Float64Array(m.simca.oCrit);

         model = m.pca;
         simca = m.simca;
         status = 'success';
         errMsg = ''
      } catch (e) {
         model = null;
         simca = null;
         status = 'error';
         errMsg = e;
      }
   }

   // promise for reading the CSV file
   function readFile (e) {
      const reader = new FileReader();
      reader.onload = () => setModel(reader.result);
      reader.readAsBinaryString(modelFile);
   }

   function reset() {
      modelFile = null
   }

   // reactive expression for tracking changes of 'dataFile'
   $: if (modelFile === null) {
      // if model file is empty reset status and error messages
      model = null;
      simca = null;
   } else {
      // if not just remove error messages if any
      status = 'neutral';
      errMsg = '';
   }

   // file name with model without extension
   $: modelFileName = modelFile ? modelFile.name.replace(/\.[^/.]+$/, "") : '';
</script>

{#if !model }

   <!-- show if data is not loaded -->
   <!-- reset component if dataFile is changed inside -->
   {#key modelFile === undefined}
   <Container>
      <FileSelector id="modelFile" bind:file={modelFile} acceptType=".json" message="Select file with model"/>
   </Container>
   {/key}

   {#if modelFile}
   <Container label="&nbsp;">
      <Button on:click={readFile}>Load</Button>
   </Container>
   {/if}

{:else}

   <!-- dataset is loaded - show info and cancel button -->
   <Container>
      <p class="model-info">
         <span>Model file:</span> <span title="{modelFileName}" class="file-name">{modelFileName}</span><br>
         <span>Target class:</span> <span title="{simca.className}" class="class-name">{simca.className}</span>
      </p>
      <ButtonCancel on:click={reset} />
   </Container>

{/if}

<!-- error message -->
{#if status === 'error' || errMsg}
<Container status="error">
   <p>{errMsg}</p>
</Container>
{/if}


<style>

   .model-info {
      line-height: 1.5em;
   }
   .model-info > :global(span) {
      vertical-align:top;
      color: #a0a0a0;
   }

   .model-info > .file-name,
   .model-info > .class-name {
      display: inline-block;
      padding: 0;
      margin: 0;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      max-width: 25ch;
      color: #222;
   }

</style>