<script>
   /****************************************************************
   * SensitivityPlot                                               *
   * --------------------                                          *
   * shows plot with sensitivity values vs. number of PCs          *
   *****************************************************************/

   import { Vector } from 'mdatools/arrays';
   import { Axes, XAxis, YAxis, Box, Lines, Segments, Legend, Rectangles, Points } from 'svelte-plots-basic/2d';

   export let params;                     // JSON with main plot parameters
   export let simcares;                   // JSON with SIMCA results
   export let simca;                      // JSON with SIMCA parameters
   export let selected;


   /*****************************************/
   /* Component code                        */
   /*****************************************/

   // constant plot parameters
   const margins = [0.85, 1.0, 0.25, 0.25];
   const showGrid = true;                  // logical, show grid or not
   const xLabel = 'Number of PCs';
   const yLabel = 'Sensitivity';
   const title = 'Sensitivity plot';

   const LINE_COLORS = {'cal': '#2679B2', 'pv': '#D22C2F', 'target': '#2679B2', 'alt': '#2679B2'};
   const FACE_COLORS = {'cal': '#f0f8ff', 'pv': '#fff8f0', 'target': '#f0f8ff', 'alt': '#f0f8ff'};

   function getSeries(res, id) {
      if (!res || !id) return null;

      const y = res.v.map(v => v.stat.sensitivity);
      const x = Vector.seq(1, y.length);
      const legendItem = {label: id, marker: 1, borderColor: LINE_COLORS[id], faceColor: FACE_COLORS[id]};
      const lineColor = LINE_COLORS[id];
      const faceColor = FACE_COLORS[id];
      return {x, y, legendItem, lineColor, faceColor};
   }

   // parameters depending on the provided results
   let resIDs, amargin;
   $: if (simcares) {
      resIDs = Object.keys(simcares);
      const nObj = simcares[resIDs[0]].nrows;
      amargin = 2 * Math.sqrt(simca.alpha/100 * (1 - simca.alpha/100) / nObj);
   } else {
      resIDs = null;
      amargin = 0;
   }

   let series, legendItems;
   $: if (resIDs && params && params.resID) {
      series = params.resID === 'both' ?
         resIDs.map(id => getSeries(simcares[id], id)) :
         [getSeries(simcares[params.resID], params.resID)];
      legendItems = series.map(s => s.legendItem);
   } else {
      series = null;
      legendItems = null;
   }

   // parameters depending SIMCA model
   let a, ncomp, aline;
   $: if (simca) {
      a = simca.ncomp;
      ncomp = simca.Nf.length;
      aline = 1 - simca.alpha/100;
   } else {
      a = null;
      aline = null;
      ncomp = null;
   }

   // reactive computation of confidence area
   $: atop = amargin + aline;
   $: abottom = aline - amargin;

   // reactive computation of y limits
   $: limYMin = params && params.zoom === 'on' ? abottom - 0.5 * amargin : -0.1;
   $: limYMax = params && params.zoom === 'on' ? atop + 0.5 * amargin : 1.2;

</script>

{#if params && simcares }
<Axes {margins} {title} {xLabel} {yLabel}
   limX={[0, ncomp + 1]}
   limY={[limYMin, limYMax]}>

   <!-- area with possible sensitivity -->
   <Rectangles left={[0]} width={[ncomp + 1]} top={[atop]} height={[2 * amargin]} faceColor='#00000010'/>
   <Segments xStart={[0]} xEnd={[ncomp + 1]} yStart={[aline]} yEnd={[aline]} lineColor='#00000050' lineType={2}/>

   <!-- selected component -->
   <Segments xStart={[a]} xEnd={[a]} yStart={[-1]} yEnd={[1.2]} lineColor='#a0a0a0' />

   <!-- data points -->
   {#each series as s}
   <Lines xValues={s.x} yValues={s.y} lineWidth={1.5} lineColor={s.lineColor} />
   <Points xValues={s.x} yValues={s.y} borderWidth={1.5} faceColor={s.faceColor} borderColor={s.lineColor}/>
   <Points xValues={[s.x.v[a - 1]]} yValues={[s.y[a - 1]]} markerSize={1.5} borderWidth={1.5} faceColor={s.lineColor} borderColor={'#a0a0a0'}/>
   {/each}

   {#if legendItems && legendItems.length > 0}
   <Legend items={legendItems} position='topright'/>
   {/if}
   <XAxis {showGrid} slot="xaxis" />
   <YAxis {showGrid} slot="yaxis" />
   <Box slot="box" />
</Axes>
{/if}