   /*************************************************************/

   import TrainAppContainer from './tab_train/AppContainer.svelte';
   import TargetAppContainer from './tab_target/AppContainer.svelte';
   import AltAppContainer from './tab_new/AppContainer.svelte';
   import WASM from './wasm/pca_wasm.js';


   let selectedApp = 'train';
   let trainAppTab;
   let targetAppTab;
   let altAppTab;

   let isInfoShown = false;

   function showInfo() {
      isInfoShown = true;
   }

   function closeInfo() {
      isInfoShown = false;
   }

   function selectTrainApp() {
      selectedApp = 'train';
   }

   function selectTargetApp() {
      selectedApp = 'target';
   }

   function selectAltApp() {
      selectedApp = 'alt';
   }

</script>

