         </p>
         <p>
            All related theory can be found in:
            <a href="https://doi.org/10.1002/cem.2506">10.1002/cem.2506</a>,
            <a href="https://doi.org/10.1002/cem.2610">10.1002/cem.2610</a>,
            <a href="https://doi.org/10.1002/cem.3250">10.1002/cem.3250</a>.
         </p>
         <h3>v. 0.9.0</h3>
         <p class="release-info">released 3/02/2024</p>
         <ul>
            <li>added <em>Distance plot</em> where you can visualize distances (<em>h</em>, <em>q</em>, <em>f</em>) separately</li>
            <li>option to show full distance (<em>f</em>) is removed from the <em>Acceptance plot</em> as redundant</li>
            <li>added possibility to see both training and PV-set results on some plots in tab <em>Train</em></li>
            <li>improvements to <em>Contribution plot</em>: now one can see the original data as well (full) and all values are shown in form of line plots with selected object highlighted</li>
            <li>fixed bug preventing selection of a point on <em>Acceptance plot</em> when log transformation is on</li>
            <li>small improvements to look and feel</li>
         </ul>
