<script>
   /*************************************************************
   * AcceptancePlotSeries                                       *
   * --------------------                                       *
   * shows plot with score and orthogonal distances             *
   **************************************************************/

   import { Vector } from 'mdatools/arrays';
   import { max } from 'mdatools/stat';
   import { getContext } from 'svelte';

   export let res;                        // JSON with PCA & SIMCA results
   export let labels;
   export let classes = [];
   export let targetClass;
   export let colors;                     // Array with colors for each category
   export let transType = 'no';           // transformation type
   export let distType = 'h/q';           // distance type
   export let showLabels = 'hide';        // show labels or not?
   export let xMax = 0;                   // to return maximum x-coordinate for series
   export let yMax = 0;                   // to return maximum y-coordinate for series


   /*****************************************/
   /* Component code                        */
   /*****************************************/

   // constant values
   const LABELS_COLOR = '#a0a0a0';

   // get axes context and adjust x margins
   const axes = getContext('axes');
   const tX = axes.tX;
   const tY = axes.tY;
   const isOk = axes.isOk;

   // convert array with colors to JSON with categories as keys
   $: col = {'alien': colors[0], 'external': colors[1]};
   $: pch = '';

   // reactive calculations for distance values
   let k, h, q, f, c, l, t, maxh, maxq, maxf;
   $: if (res) {
      const n = res.h.length;

      k = Vector.zeros(n);
      h = Vector.zeros(n);
      q = Vector.zeros(n);
      f = Vector.zeros(n);
      t = new Int32Array(n);

      if (transType === 'log') {
         for (let i = 0; i < n; i++) {
            k.v[i] = i;
            h.v[i] = Math.log(1 + res.h[i]/res.h0);
            q.v[i] = Math.log(1 + res.q[i]/res.q0);
            f.v[i] = Math.log(1 + res.f[i]);
         }
      } else {
         for (let i = 0; i < n; i++) {
            k.v[i] = i;
            h.v[i] = res.h[i]/res.h0;
            q.v[i] = res.q[i]/res.q0;
            f.v[i] = res.f[i];
         }
      }

      if (classes && classes.length === n) {
         for (let i = 0; i < n; i++) {
            t[i] = classes[i] === targetClass ? 0 : 1;
         }
      }

      c = res.a;
      l = labels;

      xMax = distType === 'h/q' ? max(h) : f.length;
      yMax = distType === 'h/q' ? max(q) : max(f);
   } else {
      h = null;
      q = null;
      f = null;
      c = null;
      l = null;
      t = null;
   }

   // reactive calculations for coordinates
   let x, y
   $: if ($isOk && h && q && f) {
      x = distType === 'h/q' ? axes.transform(h, $tX.coords) : axes.transform(k, $tX.coords);
      y = distType === 'h/q' ? axes.transform(q, $tY.coords) : axes.transform(f, $tY.coords);
   }

   const ptStyle = `dominant-baseline:middle;font-size:1em;fill:none;text-anchor:middle;cursor:default;stroke-width:2px;user-select:none;`;
   const lbStyle = `dominant-baseline:middle;font-size:0.85em;fill:${LABELS_COLOR};text-anchor:middle;cursor:default;stroke:${LABELS_COLOR};stroke-width:0.1px;user-select:none;`;
</script>

{#if $isOk && x && y}

   <g class="series series_points" title="distance_points" style={ptStyle} >
   {#each x as v, i}
      <text x={x[i]} y={y[i]} stroke={col[c[i]]} >{pch}</text>
   {/each}
   </g>

   {#if showLabels === 'show' && l && l.length > 0}
   <g class="series series_labels" title="distance_labels" style={lbStyle} >
      {#each x as v, i}
         <text x={x[i]} y={y[i]} dy={-12}>{l[i]}</text>
      {/each}
   </g>
   {/if}
{/if}

