<script>
   /*************************************************************/
   /* Svelte component for showing app plots                    */
   /*************************************************************/

	import { createEventDispatcher } from 'svelte';

   // import plot and plot controls components
   import Container from '../controls/Container.svelte';
   import Button from '../controls/Button.svelte';


   // Input parameters
   export let simcares;
   export let simca;
   export let options;

   let selected = -1;

   // event dispatcher
   const dispatch = createEventDispatcher();

   // Parameters of PNG image output
   const DPI = 600;
   const PNG_WIDTH = 3.125 * DPI; // 3.2 inches (8 cm) in 600 DPI
   const type = 'image/png';


   /*************************************************************/
   /* Functions needed to adjust DPI of resulting PNG image     */
   /* taken from: https://github.com/shutterstock/changeDPI     */
   /* and modified                                              */
   /*************************************************************/

   /**
    * Create table for PNG CRC calculation
    */
   function createPngDataTable() {
      const crcTable = new Int32Array(256);
      for (let n = 0; n < 256; n++) {
         let c = n;
         for (let k = 0; k < 8; k++) {
            c = (c & 1) ? 0xedb88320 ^ (c >>> 1) : c >>> 1;
         }
         crcTable[n] = c;
      }
      return crcTable;
   }

   /**
    * Calculate CRC for PNG image
    * @param buf image data
    */
   function calcCrc(buf) {
      let c = -1;
      let pngDataTable = createPngDataTable();
      for (let n = 0; n < buf.length; n++) {
         c = pngDataTable[(c ^ buf[n]) & 0xFF] ^ (c >>> 8);
      }
      return c ^ -1;
   }

   /**
    * Change DPI of PNG image
    * @param dataArray image data
    * @param dpi new DPI
    */
   function changeDpiOnArray(dataArray, dpi) {

      const physChunk = new Uint8Array(13);
      // chunk header pHYs
      // 9 bytes of data
      // 4 bytes of crc
      // this multiplication is because the standard is dpi per meter.
      dpi *= 39.3701;
      physChunk[0] = 'p'.charCodeAt(0);
      physChunk[1] = 'H'.charCodeAt(0);
      physChunk[2] = 'Y'.charCodeAt(0);
      physChunk[3] = 's'.charCodeAt(0);
      physChunk[4] = dpi >>> 24; // dpiX byte 3
      physChunk[5] = dpi >>> 16; // dpiX byte 2
      physChunk[6] = dpi >>> 8;  // dpiX byte 1
      physChunk[7] = dpi & 0xff; // dpiX byte 0
      physChunk[8] = physChunk[4];  // dpiY byte 3
      physChunk[9] = physChunk[5];  // dpiY byte 2
      physChunk[10] = physChunk[6]; // dpiY byte 1
      physChunk[11] = physChunk[7]; // dpiY byte 0
      physChunk[12] = 1; // dot per meter....

      // compute and add CRC for physChunk
      const crc = calcCrc(physChunk);
      const crcChunk = new Uint8Array(4);
      crcChunk[0] = crc >>> 24;
      crcChunk[1] = crc >>> 16;
      crcChunk[2] = crc >>> 8;
      crcChunk[3] = crc & 0xff;

      // chunk structur 4 bytes for length is 9
      const chunkLength = new Uint8Array(4);
      chunkLength[0] = 0;
      chunkLength[1] = 0;
      chunkLength[2] = 0;
      chunkLength[3] = 9;

      const finalHeader = new Uint8Array(54);
      finalHeader.set(dataArray, 0);
      finalHeader.set(chunkLength, 33);
      finalHeader.set(physChunk, 37);
      finalHeader.set(crcChunk, 50);
      return finalHeader;
   }

   /**
    * Change DPI of PNG image
    * @param base64Image image data
    * @param dpi new DPI
    */
   export function changeDPI(base64Image, dpi) {

      const dataSplitted = base64Image.split(',');
      const format = dataSplitted[0];
      const body = dataSplitted[1];

      // here we assume there is pHYS chunk in the header
      const headerLength = 33 / 3 * 4;

      const stringHeader = body.substring(0, headerLength);
      const restOfData = body.substring(headerLength);
      const headerBytes = atob(stringHeader);
      const dataArray = new Uint8Array(headerBytes.length);
      for (let i = 0; i < dataArray.length; i++) {
         dataArray[i] = headerBytes.charCodeAt(i);
      }
      const finalArray = changeDpiOnArray(dataArray, dpi, type);
      const base64Header = btoa(String.fromCharCode(...finalArray));
      return [format, ',', base64Header, restOfData].join('');
   }


   /*************************************************************/
   /* Functions for converting SVG object to PNG or SVG file    */
   /*************************************************************/

   // Donwload current plot as PNG image
   function downloadPNG () {

      // get reference to SVG element with current plot shown and make its copy
      const svgEl = plot_container.getElementsByTagName('svg')[0];
      const fontSize = svgEl.style.fontSize;

      // change size of the parent node to let app re-draw the plot
      svgEl.parentNode.classList.add('off-screen');

      setTimeout(() => {

         // increase text size
         svgEl.style.fontSize = '16px';

         // compute canvas size based on SVG size and desired PNG WIDTH in pixels
         const svgWidth = svgEl.clientWidth;
         const svgHeight = svgEl.clientHeight;
         const scaleFactor = PNG_WIDTH / svgWidth;

         // create a new canvas element
         const canvas = document.createElement('canvas');
         const context = canvas.getContext('2d');

         // set the canvas size to match the SVG element
         canvas.width =  svgWidth * scaleFactor;
         canvas.height = svgHeight * scaleFactor;
         svgEl.setAttribute("width", canvas.width);
         svgEl.setAttribute("height", canvas.height);

         // draw a white background
         context.fillStyle = 'white';
         context.scale(scaleFactor, scaleFactor)
         context.fillRect(0, 0, canvas.width, canvas.height);

         // draw the SVG onto the canvas
         const svgXml = new XMLSerializer().serializeToString(svgEl);
         const img = new Image();

         img.onload = function () {

            // draw the image
            context.drawImage(img, 0, 0, canvas.width, canvas.height);

            // create a data URL for the canvas and adjust its DPI
            const dataURL = canvas.toDataURL('image/png');
            const dataURL2 = changeDPI(dataURL, 600);

            // create a temporary link and trigger the download
            const downloadLink = document.createElement('a');
            downloadLink.href = dataURL2;
            downloadLink.download = 'ddcimca-plot.png';
            downloadLink.click();
         };

         // set the image source
         img.src = 'data:image/svg+xml;base64,' + btoa(unescape(encodeURIComponent(svgXml)));;

         // return to normal size and remove the off-screen class
         svgEl.style.fontSize = fontSize;
         svgEl.parentNode.classList.remove('off-screen');
      }, 20);
   }


   // function for downloading of current plot as SVG image
   function downloadSVG() {
      const svg = plot_container.getElementsByTagName('svg')[0];
      svg.setAttribute('xmlns', 'http://www.w3.org/2000/svg');
      const blob = new Blob([svg.parentNode.innerHTML], { type: 'image/svg+xml' });

      const url = window.URL.createObjectURL(blob)
      const a = document.createElement('a')
      a.setAttribute('href', url)
      a.setAttribute('download', 'ddsimca-plot.svg');
      a.click()
   }

   // selected plot number and its parameters
   let selected_plot = 0;
   let params = null;
   let plot_container;

   // function to switch between plots
   function switchPlot(i) {
      params = null;
      selected_plot = i;
   }

   // function to dispatch event when outlier is marked
   function markOutlier(el) {
      dispatch('setoutlier', {id: selected});
   }

   function plotOutlier(el) {
      dispatch('plotoutlier', {id: selected});
   }

</script>

<div class="plot-container">

   <!-- column with current plot -->
   <div bind:this={plot_container} class="plot-area">
      <svelte:component this={options[selected_plot].plotComponent} bind:selected={selected} {simcares} {simca} {params} />
   </div>

   <!-- column with plot selectors and current plot settings -->
   <div class="plot-controls-area">
      {#each options as {label, plotComponent, controlsComponent}, i}
         {#if selected_plot === i}
         <svelte:component this={controlsComponent} bind:params={params} {simcares} />
         <Container label="Download as">
            <div class="buttons">
            <Button on:click={downloadSVG}>SVG</Button>
            <Button on:click={downloadPNG}>PNG</Button>
            </div>
         </Container>

            {#if selected >= 0 && i < 2}
            <Container label="Selected">
               <div class="buttons button-outlier">
               <Button on:click={plotOutlier}>plot</Button>
               <Button on:click={markOutlier}>remove</Button>
               </div>
            </Container>
            {/if}

         {:else}
         <button class="plot-selector" on:keydown={() => switchPlot(i)} on:click={() => switchPlot(i)}>{label}</button>
         {/if}
      {/each}

   </div>
</div>


<style>
   .plot-controls-area > :global(.control-element:has(.button-outlier)) {
      background: #fff0e8;
   }
   .button-outlier  :global(span:last-child > button) {
      color: white;
      border-color: crimson;
      background: crimson;
   }

   .button-outlier  :global(span:last-child > button:hover) {
      background: #DC143C20;
      border-color: #DC143C80;
      color: crimson;
   }

   .buttons {
      position: relative;
      box-sizing: border-box;
      display: inline-block;
      flex: 1 1 auto;
      margin: 0;
      padding: 2px 0;
      width: auto;
      border-radius: 2px;
      cursor: default;
   }

   .plot-container {
      width: 100%;
      height: 100%;
      box-sizing: border-box;
      padding: 0;
      display: grid;
      grid-template-rows: 1fr;
      grid-template-columns: 1fr minmax(270px, 30%);
   }

   .plot-area {
      padding: 1em;
      margin-top: 0.25rem;
      border-radius: 0.25em;
      background: #fefefe;
   }

   .plot-controls-area {
      padding: 0 0.25em 0 0.5em;
   }

   .plot-selector {
      cursor: pointer;
      color: #606060;
      background: #fffef480;
      box-sizing: border-box;
      padding: 0.5em 1em;
      margin: 0.25em 0;
      border-radius: 0.25rem;
      box-shadow: none;
      border: none;
      width: 100%;
      text-align: left;
      font-size: 1rem;
   }

   .plot-selector:hover {
      background: #e0e0e0;
      color: #403020;
   }

   :global(div.plot-container.off-screen) {
      position:absolute;
      top:0;
      left:50%;
      width: 650px;
      height: 600px;
      background: white;
      z-index: 999;
   }

   @media(min-width:1100px) {
      .plot-container {
         grid-template-columns: 1fr minmax(285px, 30%);
      }
   }

   @media(min-width:1300px) {
      .plot-container {
         grid-template-columns: 1fr minmax(300px, 30%);
      }
   }

</style>